import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  a:any[]=[]	
  ob={id:0,name:"",email:"",s1:0,s2:0,s3:0,total:0,result:""}

  constructor()
  {
  	this.load()
  }
  insert(t1,t2,t3,t4,t5)
  {	
  this.ob.id=0
	  let id=1
  	if(this.a.length>0)
  	{
  		id=this.a[this.a.length-1].id+1
  	}	
  	let name=t1.value
  	let email=t2.value
  	let s1=+t3.value
  	let s2=+t4.value
  	let s3=+t5.value
  	let total=s1+s2+s3
  	let result=total<150?"fail":"pass"
	let ob={
		id:id,
		name:name,
		email:email,
		s1:s1,
		s2:s2,
		s3:s3,
		total:total,
		result:result
	}	
	if(this.a.filter(item=>{return item.email==ob.email}).length==0)
	{
		this.a.push(ob)	
		this.save()
	}

  }
  edit(index)
  {
  	this.ob=this.a[index]
  }
  update(t1,t2,t3,t4,t5,t6)
  {
    let id=this.ob.id
    let name=t2.value
    let email=t3.value
    let s1=+t4.value
    let s2=+t5.value
    let s3=+t6.value
    let total=s1+s2+s3
    let result=total<150?"fail":"pass"


    let ob={id:id, name:name, email:email, s1:s1, s2:s2, s3:s3, total:total, result:result }
  	for(let i=0;i<this.a.length;i++)
  	{
  		if(this.a[i].id==this.ob.id)
  		{
  			this.a[i]=ob
  			this.save()
  		}
  	}
    this.ob.id=0
  }
  del(index)
  {
  this.ob.id=0
  	this.a.splice(index,1)
  	this.save()
  }
  save()
  {
  	localStorage.a=JSON.stringify(this.a)
  }
  load()
  {
  	if(localStorage.a)
  	{
  		this.a=JSON.parse(localStorage.a)
  	}
  this.ob.id=0
  }
}
